// import .
import React from "react";
import styled from "styled-components";
import { Grid, Button } from "../../../elements/Index";
import ApplyUserModal from "../../ApplyUserModal";
import { useHistory } from "react-router";
import { apis } from "../../../lib/axios";

// PosterButton   .
const PosterButton = props => {
  console.log(props);
  const history = useHistory();

  const DeletePost = async () => {
    try {
      const deletePost = await apis.deletePost(props.post_id);
      history.push("/");
      console.log("", deletePost);
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <React.Fragment>
      <Grid display="flex" justifyContent="center">
        {props.passedData?.projectStatus === "" && (
          <Button
            common
            width="140px"
            height="35px"
            isValue="end"
            _onClick={e => {
              props.applyUserModalOpen(e.target.value);
            }}
          >
             
          </Button>
        )}
        <ApplyUserModal
          applyUserModal={props.applyUserModal}
          setApplyUserModal={props.setApplyUserModal}
          applyValue={props.applyValue}
          postId={props.post_id}
          passdedMenber={props.passdedMenber}
          statusCheck={props.statusCheck}
        />
        <ContentMedia>
          {props.passedData?.projectStatus === "" && (
            <Button
              common
              width="140px"
              height="35px"
              _onClick={() => {
                props.edit_status("");
              }}
            >
              
            </Button>
          )}
          {props.passedData?.projectStatus === "" && (
            <Button
              common
              width="140px"
              height="35px"
              isValue="submit"
              _onClick={e => {
                props.applyUserModalOpen(e.target.value);
              }}
            >
              
            </Button>
          )}
          {props.passedData?.projectStatus === "" && <div></div>}

          {props.passedData?.projectStatus === "" && (
            <Button
              common
              width="140px"
              height="35px"
              _onClick={() => {
                history.push({ pathname: `/postedit/${props.post_id}` });
              }}
            >
              
            </Button>
          )}

          {props.passedData?.projectStatus === "" && (
            <Button
              common
              width="140px"
              height="35px"
              _onClick={() => {
                history.push({ pathname: `/postedit/${props.post_id}` });
              }}
            >
              
            </Button>
          )}

          <Button
            common
            width="140px"
            height="35px"
            _onClick={() => {
              DeletePost();
              window.alert(".");
            }}
          >
            
          </Button>
        </ContentMedia>
      </Grid>
    </React.Fragment>
  );
};

const ContentMedia = styled.div`
  display: flex;
  @media screen and (max-width: 360px) {
    display: flex;
    width: 300px;
    height: 10px;
    margin-bottom: 80px;
  }
`;

// export      .
export default PosterButton;
